* {
 color: #212121;
}
.schedule {
  margin-bottom: 5px;
}
.schedule span {
  display: inline-block;
  margin-right: 20px;
  min-width: 30px;
}
.scheduleTime {
  width: 120px;
}
.container {
  display: flex;
  justify-content: center;
  align-content: center;
  width: 100%;
  height: 100vh;
  flex-wrap: wrap;
  font-size: 1.1em;
}
.search-bar {
  margin-bottom: 60px;
  text-align: center;
}
.search-bar-container {
  width: 100%;
  display: flex;
}
.search-bar-input {
  width: 400px;
  height: 60px;
  font-size: 1.1em;
  appearance: none;
  background-color: #F9F9F9;
  border-top: 1px solid #EDEDED;
  border-left: 1px solid #EDEDED;
  border-bottom: 1px solid #EDEDED;
  border-right: 0;
  border-radius: 0 10px 10px 0;
  text-indent: 15px;
  outline: 0;
  box-sizing: border-box;
}
.search-bar-skydns {
  background: #63A2C0;
  display: inline-flex;
  align-items: center;
  box-sizing: border-box;
  height: 60px;
  padding: 0 30px;
  border-radius: 10px 0 0 10px;
  color: #fff;
  font-weight: 700;
}
.favorites {
  width: 100%;
  display: flex;
  justify-content: center;

}
.favorites-column {
  min-width: 200px;
  margin: 0 40px;
}
.favorites a {
  text-decoration: none;
  color: #212121;
}
.favorites a:hover {
  color: #63A2C0;
}
h4 {
  color: #a9a8a8;
}